<template>
  <h2 :class="{'index': index}" class="header">
    <img src="@/assets/img/title.png" class="img" />
    <a v-if="!index" href="javascript:history.back(-1)" class="return">
      <i class="iconfont icon-fanhui"></i>
      <span class="text">返回</span>
    </a>
  </h2>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'Header',
    props: {
      index: {
        type: Boolean,
        default: false
      }
    },
    data () {
      return {
        msg: 'Welcome to Your Vue.js App'
      }
    }
  }
</script>

<style rel="stylesheet/less" lang="less" scoped>
  .header {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 205px;
    position: relative;
    &.index {
      justify-content: flex-start;
      box-sizing: border-box;
      padding: 0 80px;
    }
    .return {
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      left: 36px;
      width: 246px;
      height: 96px;
      color: #fff;
	    background: rgba(214, 132, 30, 0.9);
	    background-blend-mode: normal, normal;
	    border-radius: 48px;
      opacity: 0.9;
      .iconfont {
        font-size: 78px;
        margin-right: 10px;
      }
      .text {
        font-size: 40px;
      }
    }
    .img {
      display: block;
      width: 545px;
    }
  }
</style>


