<template>
  <transition name="fade">
    <section class="match-wrapper">
      <MyHeader :index="false" />
      <Subcontent ref="subcontent" :title-show="false" :title="title" :margin="0">
        <div class="match-box">
          <section class="status-box">
            <div class="avatar-box">
              <div class="avatar left">
                <div class="photo"><img :src="user1.img" /></div>
              </div>
              <p class="nickname left">{{user1.nickname}}</p>
            </div>
            <div class="score-box">
              <div class="score">
                <span :style="{'width': user1.score + '%'}" class="score-inner"></span>
              </div>
              <div class="number">
                <span>0</span>
                <span>{{user1.score}}</span>
              </div>
            </div>
            <div class="time">
              <svg class="svg" width="100%" height="100%" viewbox="0 0 162 162"
                   version="1.1" xmlns="http://www.w3.org/2000/svg">
                  <circle cx="50%" cy="50%" r="67"
                          stroke="#d3d3d3" fill="none"
                          stroke-width="27" stroke-linecap="round"
                  />
                  <circle cx="50%" cy="50%" r="67"
                          stroke="#ff776c" fill="none"
                          stroke-width="15" transform="matrix(0,-1,1,0,0,162)"
                          :style="{'stroke-dasharray': circleProgress}"
                  />
              </svg>
              <div class="time-inner">{{circleTime}}</div>
            </div>
            <div class="score-box">
              <div class="score rotate">
                <span :style="{'width': user2.score + '%'}" class="score-inner"></span>
              </div>
              <div class="number">
                <span>{{user2.score}}</span>
                <span>0</span>
              </div>
            </div>
            <div class="avatar-box">
              <div class="avatar right">
                <div class="photo"><img :src="user2.img" /></div>
              </div>
              <p class="nickname right">{{user2.nickname}}</p>
            </div>
          </section>
          <section class="test-box">
            <div class="test">
              <h4 class="test-title">第{{testIndex}}题</h4>
              <p class="test-text">{{showTest.text}}</p>
            </div>
            <ul class="choose-list">
              <li v-for="(item, index) in showTest.choose"
                  :class="{'correct': showCorrect && index + 1 == showTest.correct}" 
                  class="choose-item">
                <span class="circle"></span>
                <span class="index">{{chooseIndex[index]}}</span>
                <span class="text">{{item}}</span>
              </li>
            </ul>
          </section>
        </div>
      </Subcontent>
    </section>
  </transition>
</template>

<script type="text/ecmascript-6">
  import MyHeader from '@/components/Header/Header'
  import Subcontent from '@/base/Subcontent/Subcontent'
  import { getTest, socketMsg, playerInit } from '@/api/test'
  import { Notification } from 'element-ui'
  import io from 'io'

  export default {
    data () {
      return {
        socket: '',
        user1: {
          nickname: '',
          img: '',
          score: 0
        },
        user2: {
          nickname: '',
          img: '',
          score: 0
        },
        userObj: {
          firstUser: '',
          secondUser: ''
        },
        testObj: {
          index: 0,
          faster: ''
        },
        matchStart: false,
        title: '标题',
        chooseIndex: ['A', 'B', 'C', 'D'],
        testIndex: 1,
        tempData: [], // 修改后的数据
        showCorrect: false, // 是否显示正确选项
        img: 'https://imgsa.baidu.com/forum/w%3D580/sign=6d5c692c9222720e7bcee2f24bca0a3a/c732324e251f95cab5635ef8c3177f3e660952a6.jpg',
        score: 30,
        initPercent: 100,
        percent: 100
      }
    },
    computed: {
      // 计时进度条
      circleProgress () {
        return this.circlePercent(67, this.percent)
      },
      // 计时时间
      circleTime () {
        return Math.ceil(this.percent / 10)
      },
      // 当前显示的题目
      showTest () {
        var temp = {
          choose: [],
          chosed: false,
          correct: '1',
          id: '0',
          index: 1,
          text: ''
        }
        this.tempData.forEach((value) => {
          if (this.testIndex === value.index) {
            temp = value
          }
        })
        return temp
      }
    },
    methods: {
      // 环状进度
      circlePercent: function (radius, percent) {
          return Math.PI * 2 * radius * percent / 100 + ' ' + Math.PI * 2 * radius * (1 - percent / 100);
      },
      // 倒计时功能
      countDown (totalTime) {
        this.percent = this.initPercent // 计时初始化
        this.timer = setInterval(() => {
          this.percent = parseFloat((this.percent - 2 / totalTime).toFixed(2)) // 百分比递减
          if (this.percent === 0) {
            this._goNext()
          }
        }, 20)
      },
      // 请求数据
      getData () {
        getTest(20).then((res) => {
          if (res.staus === 200) {
            this.title = res.data.title
            if (res.data.test.length > 0) {
              let temp = res.data.test
              temp.forEach((value) => {
                value.chosed = false
              })
              this.tempData = temp
            }
          }
        })
      },
       // 建立socket连接
      setSocket () {
        // 连接服务端
        let serverdomain = 'wei.wiseljz.com'
        this.socket = io('http://' + serverdomain + ':2120')
        // 连接后登录
        this.socket.on('connect', () => {
          // console.log('监听连接');
          this.socket.emit('login', 'dp')
        })
        // 终端推送来的消息
        this.socket.on('new_msg', (msg) => {
          console.log('监听后端推送来消息');
          msg = msg.replace(/&quot;/g, "\"")
          msg = JSON.parse(msg)
          this._onMessage(msg)
        })
        // 后端推送来在线数据时
        this.socket.on('update_online_count', (online_stat) => {
          console.log('监听后端推送来的在线数据')
          console.log(online_stat)

          // 判断是否两位用户同时在线
          let onlineStatus = Object.keys(JSON.parse(online_stat))
          let pk1Status = onlineStatus.indexOf('pk1')
          let pk2Status = onlineStatus.indexOf('pk2')
          if (pk1Status > -1 && pk2Status > -1) {
            if (!this.matchStart) {
              // this.matchStart = true
              this._emitReady()
            }
          }
          if (this.matchStart && (pk1Status == -1 || pk2Status == -1)) {
            this.matchStart = false
            Notification({
              title: '提示',
              message: '检测到有用户断线，返回首页',
              type: 'warning',
              duration: 1500
            });
            setTimeout(() => {
              this._emitError()
              this.socket.emit('close', this.userid);
              playerInit()
              this.$router.go(-3)
            }, 1500)
            
          }
        })
      },
      // 清除倒计时定时
      _clearCountDown () {
        clearInterval(this.timer)
      },
      // 跳转下一页
      _goNext () {
        clearInterval(this.timer)

        // 判断是否退出倒计时
        if (this.testIndex === this.tempData.length) {
          this.socket.emit('close', this.userid);
          playerInit()
          this.$router.go(-3)
          return false
        }

        this.showCorrect = true
        setTimeout(() => {
          this.showCorrect = false
          this.testIndex++
          this.countDown(10)
        }, 800)
      },
      // 接收消息处理函数
      _onMessage (msg) {
        if (msg.origin === 'player') {
          console.log(msg)
          if (msg.status === 'userinfo') {
            this._onUserInfo(msg.data)
          }
          if (msg.status === 'test') {
            this._onTest(msg.data)
          }
        }
      },
      // 接收参赛者信息
      _onUserInfo (data) {
        let userid = data.userid
        if (this.userObj.firstUser === '') {
          this.userObj.firstUser = userid
          if (userid === 'pk1') {
            this.user1.nickname = data.nickname
            this.user1.img = data.img
          } else {
            this.user2.nickname = data.nickname
            this.user2.img = data.img
          }
        } else {
          if (this.userObj.firstUser === userid) {
            return
          }
          this.userObj.secondUser = userid
          if (userid === 'pk1') {
            this.user1.nickname = data.nickname
            this.user1.img = data.img
          } else {
            this.user2.nickname = data.nickname
            this.user2.img = data.img
          }
          this._emitStart()
          this.matchStart = true
        }
      },
      // 接收参赛者答题数据
      _onTest (data) {
        let userid = data.userid
        if (this.testObj.index + 1 == data.index) {
          if (this.testObj.faster === '') {
            this.testObj.faster = userid
            if (userid === 'pk1') {
              this.user1.score = data.score
            } else {
              this.user2.score = data.score
            }
          } else {
            if (this.testObj.faster === userid) {
              return
            }
            if (userid === 'pk1') {
              this.user1.score = data.score
            } else {
              this.user2.score = data.score
            }
            this.testObj.index = data.index
            this.testObj.faster = ''
            this._emitProceed()
          }
        }
      },
      // 发送比赛准备
      _emitReady () {
        let obj = {
          origin: 'dp',
          status: 'ready'
        }
        socketMsg(obj)
      },
      // 发送比赛开始
      _emitStart () {
        let obj = {
          origin: 'dp',
          status: 'start',
          data: {}
        }
        obj.data.user1 = this.user1
        obj.data.user2 = this.user2
        socketMsg(obj)
      },
      // 发送比赛进展
      _emitProceed () {
        let obj = {
          origin: 'dp',
          status: 'proceed',
          data: {
            user1: {
              score: this.user1.score
            },
            user2: {
              score: this.user2.score
            },
          }
        }
        socketMsg(obj)
        this._goNext()
      },
      // 发送掉线信息
      _emitError () {
        let obj = {
          origin: 'dp',
          status: 'error',
        }
        socketMsg(obj)
      }
    },
    watch: {
      matchStart: function (val, oldVal) {
        if (val) {
          this.countDown(10)
        } else {
          this._clearCountDown()
        }
      }
    },
    created () {
      this.setSocket()
      this.getData()
    },
    mounted () {
      // this.countDown(10)
    },
    beforeDestroy () {
      this.socket.emit('close', this.userid);
      playerInit()
    },
    components: {
      MyHeader,
      Subcontent
    }
  }
</script>

<style rel="stylesheet/less" lang="less" scoped>
  @import '../../assets/less/variable';
  @import '../../assets/less/mixin';

  .match-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 110;
    width: 1920px;
    height: 1080px;
    background: url('../../assets/img/bg.jpg') no-repeat;
    overflow: hidden;
    &.fade-enter-active, &.fade-leave-active {
    transition: opacity .5s;
    }
    &.fade-enter, &.fade-leave-to {
      opacity: 0;
    }
    .match-box {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 100%;
      overflow: hidden;
    }
    .status-box {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 26px;
      .avatar {
        display: flex;
        align-items: center;
        width: 283px;
        height: 177px;
        box-sizing: border-box;
        &.left {
          justify-content: flex-end;
          padding-right: 12px;
          background: #f08152;
          border-top-right-radius: 1000px;
          border-bottom-right-radius: 1000px;
        }
        &.right {
          justify-content: flex-start;
          padding-left: 12px;
          background: #fda631;
          border-top-left-radius: 1000px;
          border-bottom-left-radius: 1000px;
        }
        .photo {
          width: 142px;
          height: 142px;
          border: solid 8px #fff;
          border-radius: 50%;
          overflow: hidden;
          & > img {
            display: block;
            width: 100%;
            height: 100%;
          }
        }
      }
      .nickname {
        color: #717171;
        font-size: 30px;
        line-height: 50px;
        box-sizing: border-box;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        &.left {
          text-align: right;
          padding-right: 48px;
        }
        &.right {
          text-align: left;
          padding-left: 48px;
        }
      }
      .score {
        width: 430px;
        height: 20px;
        background-color: #c4c4c4;
        border: solid 7px #ffe2c7;
        border-radius: 100px;
        &.rotate {
          transform: rotate(180deg);
        }
        .score-inner {
          display: block;
          height: 100%;
          background-color: #feaa00;
	        border-radius: 1000px;
        }
      }
      .number {
        display: flex;
        justify-content: space-between;
        color: #e88d3e;
        font-size: 30px;
        line-height: 30px;
        margin-top: 5px;
      }
      .time {
        width: 162px;
        height: 162px;
        position: relative;
        .time-inner {
          width: 60px;
          height: 60px;
          line-height: 60px;
          text-align: center;
          color: #717171;
          font-size: 56px;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }
    }
    .test-box {
      flex: 1;
      display: flex;
      box-sizing: border-box;
      padding-top: 50px;
      .test {
        width: 800px;
        box-sizing: border-box;
        padding-bottom: 20px;
        margin: 0 125px;
        overflow: auto;
        .scroll-bar(12px, rgba(136, 136, 136, 0.22));
        .test-title {
          color: #c78300;
          font-size: 42px;
          line-height: 90px;
        }
        .test-text {
          color: #565656;
          font-size: 28px;
          line-height: 50px;
        }
      }
      .choose-item {
        display: flex;
        align-items: center;
        width: 600px;
        height: 93px;
        color: #fff;
        font-size: 32px;
        padding: 0 32px;
        margin-bottom: 21px;
        background: #bbb;
        box-shadow: 0px 3px 3px 0px rgba(116, 116, 116, 0.35);
        border-radius: 20px;
        cursor: pointer;
        &:last-child {
          margin-bottom: 0;
        }
        &.correct {
          background: #92d500;
          .circle {
            border: 6px solid #fff;
          }
        }
        .circle {
          width: 32px;
          height: 32px;
          box-sizing: border-box;
          margin-right: 30px;
          border: 6px solid #bbb;
          border-radius: 50%;
        }
        .index {
          margin-right: 20px;
        }
        .text {
          flex: 1;
          font-size: 30px;
          line-height: 32px;
        }
      }
    }
  }
</style>



