<template>
  <transition name="fade">
    <section class="mode-wrapper">
      <MyHeader :index="false" />
      <Subcontent :title="title">
        <div class="mode-box">
          <div class="mode-select">
            <p class="mdoe-select-title">学习结束，请选择测评模式</p>
            <ul class="mdoe-select-list">
              <li @click="changeType(1)"
                  :class="{'active': type === 1}"
                  class="mdoe-select-item">本机测试
              </li>
              <!-- <li @click="changeType(2)"
                  :class="{'active': type === 2}"
                  class="mdoe-select-item">单人模式
              </li> -->
              <li @click="changeType(2)"
                  :class="{'active': type === 2}"
                  class="mdoe-select-item">对战模式
              </li>
            </ul>
          </div>
          <div class="mode-entrance">
            <div @click="localTest" v-if="type === 1" class="local-test">点击前往本机测试</div>
            <div @click="ewmTest" v-if="type === 2 || type === 3" class="ewm-box">
              <div class="ewm">
                <img src="./ewm.png" />
              </div>
              <p class="text">手机扫描二维码进行测试</p>
            </div>
          </div>
        </div>
      </Subcontent>
    </section>
  </transition>
</template>

<script type="text/ecmascript-6">
  import MyHeader from '@/components/Header/Header'
  import Subcontent from '@/base/Subcontent/Subcontent'

  export default {
    props: {
      id: {
        type: [Number, String],
        default: 0
      }
    },
    data () {
      return {
        socket: '',
        title: '知识测评',
        type: 1
      }
    },
    methods: {
      changeType (type) {
        this.type = type
        if (type === 1 && this.socket) {
          this.socket.emit('close', 'dp'); // 单人时关闭socket服务
          this.socket = ''
        }
        if (type === 2) {
          this.setSocket()
        }
      },
      localTest () {
        this.$router.push({
          path: `/study/${this.id}/test`
        })
      },
      ewmTest () {
        this.$router.push({
          path: `/study/${this.id}/match`
        })
      },
      // 建立socket连接
      setSocket () {
        // 连接服务端
        let serverdomain = 'wei.wiseljz.com'
        this.socket = io('http://' + serverdomain + ':2120')
        // 连接后登录
        this.socket.on('connect', () => {
          console.log('监听连接');
          this.socket.emit('login', 'dp');
        });
        // 后端推送来消息时
        this.socket.on('new_msg', (msg) => {
          console.log('监听后端推送来消息');
          msg = msg.replace(/&quot;/g, "\"")
          msg = JSON.parse(msg)
          console.log(msg)
        });
        // 后端推送来在线数据时
        this.socket.on('update_online_count', (online_stat) => {
          console.log('监听后端推送来的在线数据');
          console.log(online_stat);
          // 判断是否两位用户同时在线
          let onlineStatus = Object.keys(JSON.parse(online_stat))
          let pk1Status = onlineStatus.indexOf('pk1')
          let pk2Status = onlineStatus.indexOf('pk2')
          if (pk1Status > -1 && pk2Status > -1) {
            this.$router.push({
              path: `/study/${this.id}/match`
            })
          }
        });
      }
    },
    created () {
      // console.log(this.id);
    },
    beforeDestroy () {
      if (this.socket) {
        this.socket.emit('close', 'dp');
      }
    },
    components: {
      MyHeader,
      Subcontent
    }
  }
</script>

<style rel="stylesheet/less" lang="less" scoped>
  @import '../../assets/less/variable';

  .mode-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 110;
    width: 1920px;
    height: 1080px;
    background: url('../../assets/img/bg.jpg') no-repeat;
    overflow: hidden;
    &.fade-enter-active, &.fade-leave-active {
    transition: opacity .5s;
    }
    &.fade-enter, &.fade-leave-to {
      opacity: 0;
    }
    .mode-box {
      display: flex;
      width: 100%;
      height: 100%;
    }
    .mode-select {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 1050px;
      .mdoe-select-title {
        color: @colorText;
        font-size: 40px;
      }
      .mdoe-select-list {
        margin-top: 70px;
      }
      .mdoe-select-item {
        width: 347px;
        height: 112px;
        line-height: 112px;
        text-align: center;
        color: #fff;
        font-size: 44px;
        margin-bottom: 54px;
        background-color: #d36f3a;
	      box-shadow: 0px 10px 0px 0px #b74d09;
        border-radius: 14px;
        cursor: pointer;
        &:last-child {
          margin-bottom: 0;
        }
        &.active {
          background-color: #f78700;
          box-shadow: 0px 10px 0px 0px #da6e00;
        }
      }
    }
    .mode-entrance {
      flex: 1;
      display: flex;
      align-items: center;
      .ewm {
        width: 290px;
        height: 290px;
        margin: 0 auto;
        border: 14px solid #f7b827;
        & > img {
          display: block;
          width: 100%;
          height: 100%;
        }
      }
      .text {
        color: #d17400;
        font-size: 34px;
        line-height: 75px;
      }
      .local-test {
        line-height: 100px;
        color: #fff;
        font-size: 40px;
        padding: 0 20px;
        background-color: #ec7036;
        border-radius: 10px;
        cursor: pointer;
      }
    }
  }
</style>



