<template>
  <transition name="fade">
    <section class="test-wrapper">
      <MyHeader :index="false" />
      <Subcontent ref="subcontent" :title="title">
        <div class="test-box">
          <h3 class="test-title">
            <span>第{{testIndex}}题</span>
            <span class="total">共{{tempData.length}}题</span>
          </h3>
          <div class="test-text">{{showTest.text}}</div>
          <ul class="test-choose-list">
            <li @click="chooseTest(index)"
                v-for="(item, index) in showTest.choose"
                :class="{'active': index === showTest.chosed}"
                class="test-choose-item">
                <p>{{chooseIndex[index]}}.  {{item}}</p>
            </li>
          </ul>
          <div class="test-option">
            <span @click="prevTest" v-if="testIndex > 1" class="btn">上一题</span>
            <span @click="nextTest" v-if="testIndex < tempData.length" class="btn">下一题</span>
            <span @click="submitTest" v-if="testIndex === tempData.length" class="btn">提交答案</span>
          </div>
        </div>
      </Subcontent>
    </section>
  </transition>
</template>

<script type="text/ecmascript-6">
  import MyHeader from '@/components/Header/Header'
  import Subcontent from '@/base/Subcontent/Subcontent'
  import {getTest} from '@/api/test'
  import {Notification } from 'element-ui'

  export default {
    props: {
      id: {
        type: [Number, String],
        default: 0
      }
    },
    data () {
      return {
        title: '标题',
        chooseIndex: ['A', 'B', 'C', 'D'],
        testIndex: 1,
        tempData: [] // 修改后的数据
      }
    },
    computed: {
      // 当前显示的题目
      showTest () {
        var temp = {
          choose: [],
          chosed: false,
          correct: '1',
          id: '0',
          index: 1,
          text: ''
        }
        this.tempData.forEach((value) => {
            if (this.testIndex === value.index) {
              temp = value
            }
        })
        return temp
      }
    },
    methods: {
      chooseTest (index) {
        this.showTest.chosed = index
      },
      prevTest () {
        if (this.testIndex === 1) {
          return
        }
        this.testIndex--
        this.$refs.subcontent.$refs.scroll.scrollTop = 0
      },
      nextTest () {
         if (this.testIndex === this.tempData.length) {
          return
        }
        this.testIndex++
        this.$refs.subcontent.$refs.scroll.scrollTop = 0
      },
      submitTest () {
        let flag = false
        let num = 0
        let curData = this.tempData
        curData.forEach((val) => {
          if (val.chosed === false) {
            flag = true
          }
        })

        if (flag) {
          Notification({
            title: '提示',
            message: '您还有尚未选择答案的题目，请点击上一页补充完整',
            type: 'warning',
            duration: 1500
          });
          return
        }
        
        curData.forEach((val) => {
          if (val.chosed == val.correct) {
            num++
          }
        })
        let content = '您的自测结果为：共答对' + num + '题，得分' + num * 5 + '分'
        this.$alert(content, '自测结果', {
          confirmButtonText: '确定',
          callback: action => {
            this.$router.go(-3)
          }
        });
      },
      // 请求数据
      getData () {
        getTest(this.id).then((res) => {
          if (res.staus === 200) {
            this.title = res.data.title
            if (res.data.test.length > 0) {
                let temp = res.data.test
                temp.forEach((value) => {
                    value.chosed = false
                })
                this.tempData = temp
            }
          }
        })
      },
    },
    created () {
      this.getData()
    },
    components: {
      MyHeader,
      Subcontent,
      Notification
    }
  }
</script>

<style rel="stylesheet/less" lang="less" scoped>
  @import '../../assets/less/variable';

  .test-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 110;
    width: 1920px;
    height: 1080px;
    background: url('../../assets/img/bg.jpg') no-repeat;
    overflow: hidden;
    &.fade-enter-active, &.fade-leave-active {
    transition: opacity .5s;
    }
    &.fade-enter, &.fade-leave-to {
      opacity: 0;
    }
    .test-box {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;
      box-sizing: border-box;
      padding: 0 63px 50px;
    }
    .test-title {
      width: 100%;
      line-height: 104px;
      text-align: center;
      color: #d17400;
      font-size: 41px;
      font-family: 'PingFang-SC-Medium';
      margin-bottom: 20px;
      position: relative;
      .total {
        position: absolute;
        top: 0;
        right: 0;
      }
    }
    .test-text {
      color: @colorText;
      font-size: 28px;
      line-height: 58px;
    }
    .test-choose-list {
      margin-top: 18px;
      .test-choose-item {
        min-width: 300px;
        text-align: center;
        color: @colorText;
        font-size: 28px;
        padding: 16px 42px;
        margin-bottom: 10px;
        background-color: #ddd;
        border-radius: 10px;
        cursor: pointer;
        & > p {
          line-height: 40px;
        }
        &:last-child {
          margin-bottom: 0;
        }
        &.active {
          background-color: #ffdda8;
        }
      }
    }
    .test-option {
      display: flex;
      justify-content: center;
      margin-top: 39px;
      .btn {
        width: 239px;
        height: 81px;
        line-height: 81px;
        text-align: center;
        color: #fff;
        font-size: 36px;
        margin-right: 21px;
        background-color: #f08151;
        border-radius: 10px;
        cursor: pointer;
        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
</style>


