<template>
  <transition name="fade">
    <section class="lesson-wrapper">
      <MyHeader :index="false" />
      <Subcontent :title="header">
        <div class="lesson-box">
          <h3 class="lesson-title">{{lesson.title}}</h3>
          <div v-html="lesson.detail" class="lesson-detail"></div>
          <div @click="goTest" class="lesson-btn">前往测试</div>
        </div>
      </Subcontent>
      <router-view :id="id"></router-view>
    </section>
  </transition>
</template>

<script type="text/ecmascript-6">
  import MyHeader from '@/components/Header/Header'
  import Subcontent from '@/base/Subcontent/Subcontent'
  let Base64 = require('js-base64').Base64;

  export default {
    props: {
      id: {
        type: [Number, String],
        default: 0
      },
      title: {
        type: String,
        default: ''
      },
      detail: {
        type: String,
        default: ''
      }
    },
    data () {
      return {
        header: '知识测评',
        lesson: {
          title: '',
          detail: ''
        }
      }
    },
    methods: {
      goTest () {
        this.$router.push({
          path: `/study/${this.id}/mode`
        })
      }
    },
    created () {
      this.lesson.detail = Base64.decode(this.detail)
    },
    components: {
      MyHeader,
      Subcontent
    }
  }
</script>

<style rel="stylesheet/less" lang="less" scoped>
  @import '../../assets/less/variable';

  .lesson-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    width: 1920px;
    height: 1080px;
    background: url('../../assets/img/bg.jpg') no-repeat;
    overflow: hidden;
    &.fade-enter-active, &.fade-leave-active {
    transition: opacity .5s;
    }
    &.fade-enter, &.fade-leave-to {
      opacity: 0;
    }
    .lesson-box {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;
      box-sizing: border-box;
      padding: 0 63px 50px;
    }
    .lesson-title {
      color: #d17400;
      font-size: 41px;
      font-family: 'PingFang-SC-Medium';
      line-height: 104px;
      margin-bottom: 20px;
    }
    .lesson-detail {
      text-indent: 2em;
      color: @colorText;
      font-size: 28px;
      font-family: 'PingFang-SC-Medium';
      line-height: 57px;
    }
    .lesson-btn {
      line-height: 73px;
      color: #fff;
      font-size: 33px;
      padding: 0 20px;
      margin-top: 50px;
      background-color: #ec7036;
      border-radius: 10px;
      cursor: pointer;
    }
  }
</style>


