<template>
  <div class="section-wrapper">
    <Subtitle :title="subtitle" />
    <div class="study">
      <ul class="lesson-list">
        <li @click="learn(item.id, item.title, item.intro)" v-for="(item, index) in lessonList" class="lesson-item">
          <p class="lesson">{{index+1}}.{{item.title}}</p>
          <span class="btn">去学习</span>
        </li>
      </ul>
    </div>
    <router-view :id="id" :title="title" :detail="intro"></router-view>
  </div>
</template>

<script type="text/ecmascript-6">
  import Subtitle from '@/base/Subtitle/Subtitle'
  import {getLesson} from '@/api/test'

  export default {
    data () {
      return {
        subtitle: '学习与测评',
        id: 0,
        title: '',
        intro: '',
        lessonList: []
      }
    },
    methods: {
      learn (id, title, intro) {
        this.id = id
        this.title = title
        this.intro = intro
        this.$router.push({
          path: `/study/${id}`
        })
      },
      getData () {
        getLesson().then((res) => {
          if (res.staus === 200) {
            this.lessonList = res.data
          }
        })
      }
    },
    created () {
      this.getData();
    },
    components: {
      Subtitle
    }
  }  
</script>

<style rel="stylesheet/less" lang="less" scoped>
  @import '../../assets/less/variable';
  @import '../../assets/less/mixin';

  .section-wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    background: #FFF;
    .study {
      flex: 1;
      box-sizing: border-box;
      padding: 0 30px;
      margin: 0 10px;
      overflow: auto;
      .scroll-bar(12px, rgba(136, 136, 136, 0.22));
    }
    .lesson-item {
      display: flex;
      align-items: center;
      padding: 20px 24px 20px 18px;
      border-bottom: 2px dashed @colorBorder;
      .lesson {
        flex: 1;
        color: #d17400;
        font-size: 33px;
        margin-right: 30px;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
      }
      .btn {
        width: 162px;
        height: 73px;
        line-height: 73px;
        text-align: center;
        color: #fff;
        font-size: 33px;
        background-color: #ec7036;
        border-radius: 10px;
        cursor: pointer;
      }
    }
  }
</style>


