<template>
  <div :style="{'background': bgColor}" class="subcontent">
    <Subtitle v-if="titleShow" :title="title" />
    <div ref="scroll" :style="{'margin': marginLen}" class="subbox">
      <slot></slot>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Subtitle from '@/base/Subtitle/Subtitle'

  export default {
    props: {
      titleShow: {
        type: Boolean,
        default: true
      },
      title: {
        type: String,
        default: '子标题'
      },
      margin: {
        type: Number,
        default: 10
      },
      bgColor: {
        type: String,
        default: '#fff'
      }
    },
    computed: {
      marginLen () {
        return '0' + this.margin
      }
    },
    components: {
      Subtitle
    }
  }
</script>

<style rel="stylesheet/less" lang="less" scoped>
  @import '../../assets/less/mixin';

  .subcontent {
    display: flex;
    flex-direction: column;
    width: 1810px;
    height: 830px;
    margin: 0 auto;
    .subbox {
      flex: 1;
      margin: 0 10px;
      position: relative;
      overflow: auto;
      .scroll-bar(12px, rgba(136, 136, 136, 0.22));
    }
  }
</style>


