import axios from 'axios'

export function getRank () {
  const url = 'http://cxdj.cmlzjz.com/home/wj/score'

  return axios.get(url).then((res) => {
    return Promise.resolve(res.data)
  })
}

export function upRank (userId, testId, score) {
  const url = 'http://cxdj.cmlzjz.com/home/wj/scoreup'

  const data = Object.assign({}, {
    user_id: userId,
    survey_id: testId,
    score: score
  })

  return axios.get(url, {
    params: data
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}


