<template>
  <h3 class="subtitle">
    <span>{{title}}</span>
  </h3>
</template>

<script type="text/ecmascript-6">
  export default {
    props: {
      title: {
        type:　String,
        default: '标题'
      }
    }
  }
</script>

<style rel="stylesheet/less" lang="less" scoped>
  .subtitle {
    width: 100%;
    color: #fff;
    font-size: 36px;
    line-height: 83px;
    box-sizing: border-box;
    padding: 0 48px;
    background: #d8a846;
  }
</style>


