import axios from 'axios'

export function getLesson () {
  const url = 'http://cxdj.cmlzjz.com/home/wj/wjlist'

  return axios.get(url).then((res) => {
    return Promise.resolve(res.data)
  })
}

export function getTest (id) {
  const url = 'http://cxdj.cmlzjz.com/home/wj/wj'

  const data = Object.assign({}, {
    id: id,
  })

  return axios.get(url, {
    params: data
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

export function socketMsg (obj) {
  let temp = JSON.stringify(obj)
  const url = 'http://wei.wiseljz.com:2121?type=publish&to=&content=' + temp
  axios.get(url).then((res) => {
    console.log(res)
  })
}

export function playerInit () {
  const url = 'http://cxdj.cmlzjz.com/home/wj/pkend'

  axios.get(url).then((res) => {
    console.log(res)
  })
}


