<template>
  <section class="section-wrapper">
    <Subtitle :title="subtitle" />
    <div class="introduce">
      <p class="intro-text">为进一步落实“两学一做”学习教育常态化制度化要求，不断提高党员自身水平和工作能力，长兴县党委以“夯实理论基础，
        提高思想觉悟”为主题，建设了党建知识学习测评系统。本系统以单选题题型的形式，考察党员对党建有关基础知识的掌握程度，
        考察内容主要涉及党章、习近平总书记系列重要讲话、十九大、“两学一做”等。</p>
      <img src="./introduce.png" class="intro-img" />
    </div>
  </section>
</template>

<script type="text/ecmascript-6">
  import Subtitle from '@/base/Subtitle/Subtitle'

  export default {
    data () {
      return {
        subtitle: '测评介绍'
      }
    },
    components: {
      Subtitle
    }
  }
</script>

<style rel="stylesheet/less" lang="less" scoped>
  @import '../../assets/less/variable';

  .section-wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.7);
    .introduce {
      flex: 1;
      overflow: hidden;
      box-sizing: border-box;
      padding: 0 38px;
      .intro-text {
        color: @colorText;
        font-family: MicrosoftYaHei;
        font-size: 26px;
        line-height: 50px;
        letter-spacing: 1px;
        margin: 47px 0 62px;
      }
      .intro-img {
        display: block;
        margin: 0 auto;
      }
    }
  }
</style>



